let swiper = new Swiper('.sliderbox', {
  loop: true,
  effect: 'fade',
  autoHight: true,

  // If we need pagination
  pagination: {
    el: '.swiper-pagination',
    clickable: true,
  },

});

let carousel = new Swiper('.carouselbox', {
  spaceBetween: 30,
  slidesPerView: 2,
  centeredSlides: true,

  // If we need pagination
  navigation: {
    nextEl: '.swiper-button-next',
    prevEl: '.swiper-button-prev',
  },
  breakpoints: {
    481: {
      slidesPerView: 1,
      slidesPerGroup: 1,
      centeredSlides: false,
    },
    640: {
      slidesPerView: 2,
      slidesPerGroup: 2,
      centeredSlides: false,
    },
    992: {
      slidesPerView: 4,
      slidesPerGroup: 4,
      centeredSlides: false,
    }
  }


});

let thumbImage = new Swiper('.thumbnail-image', {
  // loop: true,
  direction: 'vertical',
  spaceBetween: 15,
  slidesPerView: 10,
  freeMode: true,
  watchSlidesProgress: true,




});

let mainImage = new Swiper('.main-image', {
  loop: true,
  autoHeight:true,

  pagination: {
    el: '.swiper-pagination',
    clickable: true,
  },
  thumbs: {
    swiper: thumbImage,
  },



});

