import vars from "../_vars";





// mobile menu
vars.$submenu.forEach((menu) => menu.addEventListener('click', function(e) {
  e.preventDefault()
  vars.$submenu.forEach((item) => item != item ? item.closest('.has-child').classList.remove('active') : null)
  if(this.closest('.has-child').classList != 'active') {
    this.closest('.has-child').classList.toggle('active')
  }
}))

// console.log(vars.$submenu)


// tabbed

const trigger = document.querySelectorAll('.tabbed-trigger'),
      content = document.querySelectorAll('.tabbed > div')
console.log(content )
trigger.forEach((btn) => {
  btn.addEventListener('click', function() {
    let dataTarget = this.dataset.id,
        body = document.querySelector(`#${dataTarget}`)

    trigger.forEach((b) => b.parentNode.classList.remove('active'))
    content.forEach((s) => s.classList.remove('active'))
    this.parentNode.classList.add('active')
    body.classList.add('active')


  })
})



