import vars from "../_vars";



for (let i =0; i < vars.$triggerOpen.length; i++) {
  let currentId = vars.$triggerOpen[i].dataset.target,
  targetEl = document.querySelector(`#${currentId}`)
  console.log(currentId)


  const openData = function() {
    targetEl.classList.remove('active')
    vars.$overlay.classList.remove('active')

  };
  vars.$triggerOpen[i].addEventListener('click', function() {
    targetEl.classList.add('active')
    vars.$overlay.classList.add('active')

  });

  targetEl.querySelector('[close-button]').addEventListener('click', openData)
  vars.$overlay.addEventListener('click', openData)

}


